			"title": "Rattlehead",
			"description": "The Immortal Vic Rattlehead can be seen clutching a radiant green crystal of cosmic nature, sailing the seas on a rusted ship. His credo is, \"See no evil, hear no evil, speak no evil.\" Vic navigates in search of retribution and destruction.",
